.MainDash {
    flex-grow: 1; /* This allows it to take up the rest of the space beside the sidebar */
    display: flex;
    flex-direction: column;
    background-color: #eaf0f7; /* Ensures background color is kept as needed */
    padding: 3.5rem 2%;
    margin-left: 220px;
}


.MainDash h1{
    margin-left: 20px;
    color: #1F2B6C;
    font-size: 1.8rem;
}

@media screen and (max-width: 1200px) {
    .MainDash{
        justify-content:flex-start;
    }
}

@media screen and (max-width: 768px)
{
    .MainDash{
        align-items: center;
    }
}

@media screen and (max-width: 1200px) {

    .MainDash{
        margin-left: 100px;
    }
  }
